import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { FormattedMessage, intlShape, injectIntl } from 'react-intl';
import _ from 'lodash';
import Spinner from 'ui-library/lib/components/general/Spinner';

import AdminActions from '../shared/admin-actions/AdminActions';
import GeneratedPasswordModal from '../shared/GeneratedPasswordModal';
import AlertModal from '../shared/AlertModal';
import {
  getUserAction,
  editUserAction,
  clearNewUserAction,
} from '../../store/actions/userActions';
import { getSearchResults } from '../../store/actions/search/search';
import EditModelPage from '../../containers/EditModelPage/EditModelPage';
import { MODEL_TYPES } from '../../utils/modelTypes';
import { getTitleOrPrimaryAttributeValue } from '../../utils/helpers';
import * as routes from '../../utils/routes';
import permissions, { PERMISSIONS } from '../../utils/permissions/permissions';

import './UserProfilePage.css';

export class UserProfilePage extends Component {
  constructor(props) {
    super(props);

    const isNewUser = !!this.props.newUser;
    const newPassword = _.get(this.props, 'newUser.attributes.dadmin-generated-password[0]', undefined);

    this.toggleModal = this.toggleModal.bind(this);
    this.handleSearch = this.handleSearch.bind(this);
    this.handleClearSearch = this.handleClearSearch.bind(this);

    this.state = {
      isLoading: true,
      newPassword,
      renderNewPasswordModal: isNewUser && !_.isUndefined(newPassword),
      renderAlertModal: isNewUser && _.isUndefined(newPassword),
    };
  }

  componentDidMount() {
    this.getUser();
  }

  async getUser() {
    const { id, type } = this.props.match.params;

    try {
      await this.props.getUserAction(id, type);
      this.setState({ isLoading: false });
    } catch (e) {
      // Errors caught in actions
    }
  }

  handleUserEdit(...args) {
    this.props.editUserAction(args);
  }

  handleSearch(queryString = '', options = {}) {
    this.props.getSearchResults(queryString, {
      ...options,
    });
  }

  handleClearSearch(options = {}) {
    this.handleSearch('', {
      skipMessage: true,
      hideSpinner: true,
      ...options,
    });
  }


  toggleModal() {
    this.setState({ renderNewPasswordModal: false, renderAlertModal: false }, () => {
      this.props.clearNewUserAction();
    });
  }

  render() {
    const {
      intl,
      user,
      meta,
      userGroups,
      error,
      errorAttrs,
    } = this.props;
    const {
      isLoading,
      newPassword,
      renderNewPasswordModal,
      renderAlertModal,
    } = this.state;

    if (isLoading) return <Spinner show />;

    const userName = getTitleOrPrimaryAttributeValue(user.attributes, meta);
    const pageHeaderChildren = (
      <AdminActions
        userId={user.id}
        isLocked={user.attributes['dadmin-account-locked'] ? false : user.attributes['dadmin-account-locked'][0]}
        isDisabled={_.isUndefined(user.attributes['ds-pwp-account-disabled']) ? false : user.attributes['ds-pwp-account-disabled'][0]}
      />
    );
    const children = (
      <>
        <GeneratedPasswordModal
          isOpen={renderNewPasswordModal}
          modalTitle={intl.formatMessage({ id: 'components.generated-password-modal.user-creation.title' })}
          modalDescription={<FormattedMessage id="components.generated-password-modal.description" />}
          toggleModal={this.toggleModal}
          password={newPassword}
        />

        <AlertModal
          isOpen={renderAlertModal}
          modalTitle={intl.formatMessage({ id: 'page.user-profile.alert-modal.title' })}
          modalDescription={intl.formatMessage({ id: 'page.user-profile.alert-modal.message' })}
          onSave={this.toggleModal}
          saveText={intl.formatMessage({ id: 'common.close' })}
        />
      </>
    );

    const canUpdate = permissions.hasPermission(PERMISSIONS.UPDATE, user.type);
    const canManage = permissions.manageGroups();

    return (
      <EditModelPage
        profilePageModel={MODEL_TYPES.USERS}
        model={user}
        modelMeta={meta}
        modelDisplayAttributeValue={userName}
        membershipResults={userGroups}
        editModelAction={this.props.editUserAction}
        onSearch={this.handleSearch}
        onClearSearch={this.handleClearSearch}
        pageHeaderChildren={pageHeaderChildren}
        error={error}
        errorAttrs={errorAttrs}
        breadcrumbPath={routes.USERS_SEARCH_ROUTE}
        canUpdateModel={canUpdate}
        canManageMembershipForModel={canManage}
      >
        {children}
      </EditModelPage>
    );
  }
}

UserProfilePage.propTypes = {
  intl: intlShape.isRequired,
  editUserAction: PropTypes.func.isRequired,
  getUserAction: PropTypes.func.isRequired,
  clearNewUserAction: PropTypes.func.isRequired,
  getSearchResults: PropTypes.func.isRequired,
  match: PropTypes.shape({
    params: PropTypes.shape({
      id: PropTypes.string.isRequired,
      type: PropTypes.string.isRequired,
    }).isRequired,
  }).isRequired,
  user: PropTypes.shape({
    id: PropTypes.string.isRequired,
    type: PropTypes.string.isRequired,
    attributes: PropTypes.shape({}).isRequired,
    included: PropTypes.arrayOf(PropTypes.shape({})),
  }),
  meta: PropTypes.shape({
    attributes: PropTypes.shape({}).isRequired,
    included: PropTypes.shape({}),
  }),
  error: PropTypes.string,
  errorAttrs: PropTypes.arrayOf(PropTypes.string),
  userGroups: PropTypes.shape({
    dataType: PropTypes.string,
    queryString: PropTypes.string.isRequired,
    results: PropTypes.arrayOf(PropTypes.shape({})),
    included: PropTypes.arrayOf(PropTypes.shape({})),
    meta: PropTypes.shape({}),
    primarySearchAttribute: PropTypes.string,
    groupTitleAttribute: PropTypes.string,
    error: PropTypes.string,
  }),
  newUser: PropTypes.shape({
    id: PropTypes.string.isRequired,
    attributes: PropTypes.shape({
      'dadmin-generated-password': PropTypes.arrayOf(PropTypes.string),
    }).isRequired,
  }),
};

UserProfilePage.defaultProps = {
  user: undefined,
  error: undefined,
  meta: undefined,
  errorAttrs: undefined,
  userGroups: undefined,
  newUser: undefined,
};

function mapStateToProps(state) {
  const {
    user,
    included,
    errorAttrs,
    userGroups,
    error,
    meta,
    newUser,
  } = state.user;
  const completeUser = user && {
    ...user.data,
    included,
  };

  return {
    error,
    meta,
    userGroups,
    user: completeUser,
    errorAttrs,
    newUser: _.get(newUser, 'data'),
  };
}

export default injectIntl(connect(
  mapStateToProps,
  {
    getUserAction,
    editUserAction,
    clearNewUserAction,
    getSearchResults,
  },
)(UserProfilePage));
