import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import { createGenericAction } from '../../store/actions/generics';
import NewGenericPage from '../../components/generic/GenericCreatePage/GenericCreatePage';

export const GenericCreatePageContainer = props => (
  <NewGenericPage
    {...props}
  />
);

GenericCreatePageContainer.propTypes = {
  createAction: PropTypes.func.isRequired,
  errorAttrs: PropTypes.arrayOf(PropTypes.string),
  match: PropTypes.shape({
    params: PropTypes.shape({
      type: PropTypes.string,
    }).isRequired,
  }).isRequired,
};

GenericCreatePageContainer.defaultProps = {
  errorAttrs: undefined,
};

function mapStateToProps(state) {
  const { errorAttrs } = state.group;

  return {
    errorAttrs,
  };
}

export default connect(
  mapStateToProps,
  {
    createAction: createGenericAction,
  },
)(GenericCreatePageContainer);
